<template>
<div class="teachers1 padding students" v-loading.body="bodyLoading">
    <crumb style="padding: 0 5px;">
        <div class="new--btn" slot="right" @click="openAdd" v-havaRole="'courseQualityQuestionnaireAdd'">+ 新建</div>
    </crumb>
    <div class="teachers1-inp">
        <el-form :inline="true" :model="formInline" class="demo-form-inline">
            <span class="ssssss">学年：</span>
            <el-select v-model="value" placeholder="请选择学年" class="pppp" @change="luiii">
                <el-option v-for="item in options" :key="item.id" :label="item.name" :value="item.id"></el-option>
            </el-select>
            <span class="sssss">学期：</span>
            <el-select v-model="value1" placeholder="请选择学期" class="pppp" @change="termscahnge">
                <el-option v-for="item in options1" :key="item.code" :label="item.value" :value="item.code"></el-option>
            </el-select>
            <el-form-item label="课程搜索：">
                <el-input v-model="formInline.user" placeholder="请输入搜索关键词"></el-input>
            </el-form-item>
            <div class="search--btn" @click="getall" style="margin: 6px 0 0 20px">搜索</div>
        </el-form>
    </div>

    <div class="teachers1-tab">
        <template>
            <el-table :data="tableData" stripe style="width: 100%">
                <el-table-column label="课程编码">
                    <template slot-scope="scope">
                        <span>{{ scope.row.code }}</span>
                    </template>
                </el-table-column>
                <el-table-column label="课程名称" >
                    <template slot-scope="scope">
                        <div @click="jump(scope.row.course_id,scope.row.id)" style="cursor: pointer;">
                            <span>{{ scope.row.name }}</span>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column label="课程分类" >
                    <template slot-scope="scope">
                        <span>{{ scope.row.pname }}</span>
                    </template>
                </el-table-column>
                <el-table-column label="学年" >
                    <template slot-scope="scope">
                        <span>{{scope.row.grade}} - {{Number(scope.row.grade) + 1}}学年</span>
                    </template>
                </el-table-column>
                <el-table-column label="考核方式" >
                    <template slot-scope="scope">
                        <span>{{ scope.row.ppname }}</span>
                    </template>
                </el-table-column>
                <el-table-column label="配置状态"  align="center">
                    <template slot-scope="scope"> 
                        <span v-if="scope.row.finish == 0">已配置</span>
                        <span v-if="scope.row.finish == 1">未配置</span>
                    </template>
                </el-table-column>
                <el-table-column label="配置方式" align="center">
                    <template slot-scope="scope"> 
                        <span v-if="scope.row.config_type == 0">统一配置</span>
                        <span v-if="scope.row.config_type == 1">单独配置</span>
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="350">
                    <template slot-scope="scope">
                        <el-button type="text" size="small" @click="handleEdit(scope.row,index)">
                            <SvgIcon icon-class="iconbianji1"></SvgIcon>&nbsp;&nbsp;编辑
                        </el-button>
                        <el-button style="color: #ff5858;" type="text" size="small" @click="handleDelete(scope.row)">
                            <SvgIcon icon-class="iconshanchu"></SvgIcon>&nbsp;&nbsp;删除
                        </el-button>
                        <el-button type="text" size="small" @click="change(scope.$index, scope.row)">
                            <SvgIcon icon-class="iconkechengshezhi"></SvgIcon>&nbsp;&nbsp;配置考察内容与课程目标关系
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
        </template>
    </div>
    <!-- 新增编辑 -->
    <el-dialog :title="dialogTitle" :visible.sync="userDialogFormVisible" :before-close="handleDialogClose" class="set-el-dialog" :close-on-click-modal='false'>
        <el-form :model="form" :rules="rules" ref="form" label-width="120px">
            <el-form-item label="选择课程：" prop="classifyName">
                <el-select v-model="form.classifyName" placeholder="请选择课程" size="small" style="width: 100%" @change="changgrades">
                    <el-option v-for="item in curriculum" :key="item.id" :label="item.name" :value="item.id">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="选择学年：" prop="remarks">
                <el-select v-model="form.remarks" placeholder="请选择学年" size="small" style="width: 100%">
                    <!-- <el-option v-for="item in options2" :key="item" :label="item" :value="item">
                    </el-option> -->
                    <el-option v-for="item in options2" :key="item" :label="`${item} - ${item + 1}学年`" :value="item"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="配置方式：" prop="remarks3">
                <el-radio-group v-model="form.remarks3">
                    <el-radio :label="0">统一配置</el-radio>
                    <el-radio :label="1">单独配置</el-radio>
                </el-radio-group>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="cancel('form')">取 消</el-button>
            <el-button type="primary" @click="create('form')">确 定</el-button>
        </div>
    </el-dialog>
    <el-dialog title="选择教师" :visible.sync="userDialogFormVisible1" :before-close="handleDialogClose1" class="set-el-dialog" :close-on-click-modal='false'>
        <div class="teachers1_name" v-for="(item, index) in teacherlist" :key="index" @click="create1(item)">
            {{item.name}}
        </div>
    </el-dialog>
    <div>
        <template>
            <div class="block">
                <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage4" :page-sizes="[15, 30, 40, 50]" :page-size="pageSize" layout="prev, pager, next, jumper, sizes, total" :total="total"></el-pagination>
            </div>
        </template>
    </div>
</div>
</template>

<script>
import {
    setTimeout
} from "timers";
import Crumb from '@/components/Crumb'
export default {
    components: {
        Crumb
    },
    data() {
        return {
            userDialogFormVisible: false,
            userDialogFormVisible1: false,
            isAdd: true,
            rules: {
                classifyName: [{
                    required: true,
                    message: '请选择课程',
                    trigger: 'blur'
                }],
                remarks: [{
                    required: true,
                    message: '请选择学年',
                    trigger: 'blur'
                }],
                remarks3: [{
                    required: true,
                    message: '请选择配置方式：',
                    trigger: 'blur'
                }]
            },
            currentPage4: 1,
            pageSize: 15,
            total: 0,
            file: "",
            index: 1,
            formInline: {
                user: ""
            },
            tableData: [],
            options: [],
            value: "",
            options1: [
                {
                    code:'0',
                    value:'请选择'
                },
                {
                    code:'1',
                    value:'第一学期'
                },
                {
                    code:'2',
                    value:'第二学期'
                },
            ],
            curriculum: [],
            value1: "",
            options2: [],
            value2: "",
            form: {
                file: "",
                classifyName: '',
                remarks: '',
                remarks3: '0',
                id:'',
                teacherId:''
            },
            arr: "",
            bodyLoading: false,
            object:{},
            teacherlist:[],
            roleName:localStorage.getItem('roleName')
        };
    },
    computed: {
        dialogTitle() {
            return this.isAdd ? '新建试卷配置' : '编辑试卷配置'
        }
    },
    methods: {
        cancel(formName) {
            this.userDialogFormVisible = false;
            this.$refs[formName].resetFields();
        },
        cancel1(formName) {
            this.userDialogFormVisible1 = false;
            this.$refs[formName].resetFields();
        },
        //  教师 接口/teacher/examgoal/allteams/{courseId}/{grade}
        allteacher(){
            this.$get(`/teacher/examgoal/allteams/${this.object.course_id}/${this.object.grade}`).then(res=>{
                if(res.status == 1){
                    this.teacherlist = res.data
                }
            })
        },
        //   新增接口  /course/examination/add
        addexamination() {
            let key = {
                courseId: this.form.classifyName,
                grade: this.form.remarks,
                configType: this.form.remarks3
            }
            this.$post('/basic/sys/tracourseconfig/add', key).then(res => {
                if(res.status == 1){
                    this.$notify.success({
                        title: 'success',
                        duration:1500,
                        message: '新增成功'
                    })
                    this.getall()
                    this.userDialogFormVisible = false;
                }else{
                    this.$notify.error({
                        title: 'error',
                        duration:1500,
                        message: '新增失败'
                    })
                }
            })
        },
        //  编辑接口 
        userEdit(){
            let key = {
                id: this.form.id,
                courseId: this.form.classifyName,
                grade: this.form.remarks,
                configType: this.form.remarks3
            }
            this.$post('/basic/sys/tracourseconfig/edit',key).then(res=>{
                if(res.status == 1){
                    this.$notify.success({
                        title: 'success',
                        duration:1500,
                        message: '编辑成功'
                    })
                    this.getall();
                    this.userDialogFormVisible = false;
                }else{
                    this.$notify.error({
                        title: 'error',
                        duration:1500,
                        message: '编辑失败'
                    })
                }
            })
           
        },
        // 确定添加
        create(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    if (this.isAdd) {
                        this.addexamination()
                    } else {
                        this.userEdit()             
                    }
                }
            });
        },
        create1(item) {
            this.$router.push({
                // path: "/Construction/Toconfigure", // 以前的配置页面
                path: '/Construction/Toconfiguretwo', //新写的配置页面
                query: {
                    id: this.object.course_id,
                    name:this.object.name,
                    teacherId:item.teacherId,
                    type:'查看',
                    grade:this.object.grade
                }
            });
            this.userDialogFormVisible1 = false
        },
        openAdd() {
            this.userDialogFormVisible = true;
            this.form = {
                classifyName: '',
                remarks: '',
                remarks3: ''
            };
            this.isAdd = true;
        },
        handleDialogClose() {
            this.$refs['form'].resetFields();
            this.userDialogFormVisible = false;
        },
        handleDialogClose1() {
            this.userDialogFormVisible1 = false;
        },
        luiii(value) {
            if (value == "请选择学期") {
                this.options1 = [];
                this.options1.push("请选择");
            } else {
                this.arr = "abc";
                this.getall();
            }
        },
        //  /course/course/list   选择课程
        getcourse() {
            this.$get('/basic/scheduling/courseByLeader ').then(res => {
                if (res.status === 1) {
                    this.curriculum = res.data
                }
            })
        },
        //所有学年
        getgrades() {
            this.$get(`/basic/scheduling/grades/${localStorage.getItem('specialtyId')}`).then(res => {
                if (res.status === 1) {
                    const allArr = {
                        name: "全部学年",
                        id:''
                    };
                    let arr = []
                    res.data.forEach(element => {
                        arr.push({
                            id:element,
                            name:`${element}-${element + 1}学年`
                        })
                    });
                    this.options = arr;
                   this.options.unshift(allArr);
                } else {
                    this.$notify.error({
                        title: "error",
                        duration: 1500,
                        message: res.message
                    });
                }
            });
        },
        //所有学年
        changgrades(courseId) {
            this.$get(`/basic/scheduling/grades/${localStorage.getItem('specialtyId')}/${courseId}`).then(res => {
                if (res.status === 1) {
                    const allArr = {
                        name: "请选择"
                    };
                    this.options2 = JSON.parse(JSON.stringify(res.data))
                    res.data.unshift(allArr);
                    // this.options = res.data;
                } else {
                    this.$notify.error({
                        title: "error",
                        duration: 1500,
                        message: res.message
                    });
                }
            });
        },
        termscahnge() {
            this.getall();
        },
        // 搜索链接    /teacher/mycourse/bycondition
        getall() {
            this.bodyLoading = true
            let key = {
                grade:this.value,
                term:this.value1,
                name:this.formInline.user,
                page:this.currentPage4,
                rows:this.pageSize
            }
            this.$post("/basic/sys/tracourseconfig/findCourseConfigList", key).then(res => {
                if (res.status == 1) {
                    this.tableData = res.data.list;
                    this.total = Number(res.data.total);
                }
                this.bodyLoading = false
            }).catch(err => {
                this.bodyLoading = false
            })
        },
        handleEdit(row, index) {
            this.form.classifyName = row.course_id
            this.form.remarks = row.grade
            this.form.remarks3 = row.config_type
            this.form.id = row.id
            let obj = {
                type1: '',
                type: ''
            }
            this.userDialogFormVisible = true;
            this.isAdd = false;
            //编辑对话框显示值
        },
        change(index, row) {
            this.object = row
            this.allteacher()
            if(this.roleName === '教师'){
                this.$router.push({
                        // path: "/Construction/Toconfigure", // 以前的配置页面
                    path: '/Construction/Toconfiguretwo', //新写的配置页面
                    query: {
                        id: row.course_id,
                        name:row.name,
                        teacherId:localStorage.getItem('userId'),
                        grade:row.grade
                    }
                });
            }else{
                if(row.config_type === 0 ){
                    if(row.leader === localStorage.getItem('userId')){
                        this.userDialogFormVisible1 = true
                        this.$router.push({
                            // path: "/Construction/Toconfigure", // 以前的配置页面
                            path: '/Construction/Toconfiguretwo', //新写的配置页面
                            query: {
                                id: row.course_id,
                                name:row.name,
                                teacherId:localStorage.getItem('userId'),
                                grade:row.grade
                            }
                        });
                    }else{
                        this.$notify({
                            title: '提示',
                            message: '亲！您没有该权限!',
                            type: 'warning',
                            duration: 1500
                        })
                    }
                }else if(row.config_type ===1) {
                    if(row.leader === localStorage.getItem('userId')){
                        this.userDialogFormVisible1 = true
                    }else {
                        row.teams.split(',').forEach(item => {
                            if(item === localStorage.getItem('userId')){
                                this.$router.push({
                                    // path: "/Construction/Toconfigure", // 以前的配置页面
                                    path: '/Construction/Toconfiguretwo', //新写的配置页面
                                    query: {
                                        id: row.course_id,
                                        name:row.name,
                                        teacherId:localStorage.getItem('userId'),
                                        grade:row.grade
                                    }
                                });
                            }else {
                                this.$notify({
                                    title: '提示',
                                    message: '亲！您没有该权限!',
                                    type: 'warning',
                                    duration: 1500
                                })
                            }
                        });
                    }
                }
            }
        },
        // 路由传参
        jump(id, schedulingId) {
            this.$router.push({
                path: "/studentside/graduation",
                query: {
                    id: id,
                    schedulingId: schedulingId
                }
            });
        },
        //删除 /basic/sys/tracourseconfig/del/647481788117024769  
        userDel(id){
            this.$deletes(`/basic/sys/tracourseconfig/del/${id}`).then(res=>{
                if(res.status == 1){
                    this.$notify.success({
                        title: 'success',
                        duration:1500,
                        message: '删除成功'
                    })
                    this.getall();
                }else{
                    this.$notify.error({
                        title: 'error',
                        duration:1500,
                        message: '删除失败'
                    })
                }
            })
        },
        handleDelete(row) {
            this.$confirm('确定删除该课程?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.userDel(row.id)
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },
        handleSizeChange(val) {
            this.pageSize = val;
            this.getall();
        },
        handleCurrentChange(val) {
            this.currentPage4 = val;
            this.getall();
        }
    },
    mounted() {
        this.getall();
        // this.schedulingGrades();
        this.getcourse()
        this.getgrades()
    }
};
</script>

<style lang="scss">
.teachers1 {
    .teachers1-inp {
        width: 100%;
        overflow: hidden;
        background-color: #fff;
        padding: 13px 20px 15px;
        margin-bottom: 20px;
        box-sizing: border-box;
        border-radius: 10px;

        .pppp {
            // width: 15%;
            float: left;
            margin: 0;
            margin-top: 6px;
        }
    }

    .teachers1-tab {
        background-color: #fff;
        padding-bottom: 20px;
        padding-bottom: 0;
        border-radius: 10px 10px 0 0;
        .el-table .cell{
            padding-right: 0px;
            padding-left: 5px;
        }
    }

    .ssssss {
        float: left;
        font-size: 14px;
        line-height: 45px;
        font-weight: bold;
        color: #696969;
        margin-top: 3px;
    }

    .sssss {
        margin-left: 16px;
        float: left;
        font-size: 14px;
        line-height: 45px;
        font-weight: bold;
        color: #696969;
        margin-top: 3px;
    }

    .el-form--inline .el-form-item {
        margin-left: 20px;
        margin-top: 6px;
        margin-bottom: 0;
    }

    .span-dv {
        cursor: pointer;
        color: #000;
        padding: 0 8px;
    }

    .teachers1-btn {
        margin-top: 18px;
    }

    .teachers1-btn1 {
        margin-top: 18px;
    }
    .teachers1_name {
        padding: 3px 20px;
        background: rgba(3, 177, 135, 0.1);
        border: 1px solid rgba(3, 177, 135, 1);
        border-radius: 5px;
        display: inline-block;
        text-align: center;
        line-height: 26px;
        color: rgba(3, 177, 135, 1);
        margin-right: 15px;
        cursor: pointer;
    }
    .block {
        float: left;
        width: 100%;
        text-align: right;
        background-color: #fff;
        padding: 15px;
        box-sizing: border-box;
        border-radius: 0 0 10px 10px;
        margin-bottom: 100px;
    }

    .steps-dv1 {
        position: relative;

        .steps-btn {
            position: absolute;
            top: 66px;
            left: 54px;
        }
    }

    .el-dialog {
        width: 33%;
    }

    .el-button {
        padding-top: 6px;
        padding-bottom: 6px;
    }
}
</style>



